body {
  line-height: 1.5;
  font-weight: 400;
}
.navigator {
  width: 1500px;
  display: flex;
  padding: 0 0 0 8%;
  .nav_logo {
    margin: 1px 0 0;
    padding: 16px 22px 16px 10px;
  }
  .menu_items {
    width: 826px;
    padding-bottom: 8px;
    display: flex;
    align-items: center;
    font-size: 13px;
    li {
      padding: 16px 8px 10px;
      a {
        color: #262626;
        text-decoration: none;
      }
    }
  }
  img {
    width: 108px;
    height: 23px;
    margin: -3px 0 0;
  }
  .actions {
    display: flex;
    margin-left: 0;
    a {
      text-decoration: none;
      color: #262626;
    }
    .ms_all_nav {
      font-size: 15px;
    }
    .shopping_cart {
      font-size: 13px;
    }
    .login {
      font-size: 13px;
    }
  }
}
.main_image {
  height: 700px;
  width: 100%;
  position: relative;
  .swiper-container {
    .swiper-wrapper {
    }
  }
  .main-image-text {
    width: 440px;
    z-index: 1;
    left: 200px;
    position: absolute;
    top: 140px;
    cursor: pointer;
    h1 {
      font-size: 32px;
      font-weight: 600;
      margin-bottom: 20px;
    }
    p {
      font-size: 16px;
      margin-bottom: 20px;
    }
    .shopping-btn {
      outline: none;
      padding: 8px 16px;
      background-color: #0067b8;
      color: white;
      border: 0px;
      border-radius: 2px;
      font-weight: 600;
      cursor: pointer;
    }
  }
  .swiper-pagination {
    bottom: 40px;
    left: 0;
    right: 0;
    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      border: 1px solid black;
      background-color: black !important;
      margin: 0 12px;
    }
  }
  .swiper-pagination-bullet:last-child {
    margin-right: 0;
  }
}
.link_list {
  ul {
    display: flex;
    padding: 60px 75px;
    justify-content: center;
    li {
      margin: 16px 24px;
      font-weight: 600;
      .link-img {
        text-align: center;
      }
      a {
        color: #0067b8;
      }
    }
  }
}
.card_layout {
  .inner {
    padding: 75px;
    display: flex;
    justify-content: center;
    .card {
      padding: 0 15px;
      h2 {
        font-size: 29px;
        font-weight: 600;
        margin-top: 30px;
      }
      p {
        line-height: 1.5;
      }
      .footer {
        background-color: transparent;
        margin: 16px 0 0 0;
        a {
          text-decoration: none;
          color: #0067b8;
          font-weight: 700;
        }
      }
    }
  }
  .card_img {
    width: 320px;
    height: 180px;
    h2 {
      font-size: 29px;
      font-weight: 600;
      margin: 16px 0;
    }
    p {
      font-size: 16px;
      font-weight: 400;
      line-height: 1.5;
    }
    card {
      margin-bottom: 16;
    }
  }
}
.business_card_layout {
  margin: 48px;
  .business_card_title {
    font-weight: 600;
    line-height: 1.2;
    font-size: 35px;
    position: relative;
    top: 20px;
    left: 30px;
  }
  .inner {
    padding: 75px;
    display: flex;
    justify-content: center;
    .card {
      padding: 0 15px;
      .badge {
        background-color: #ffb900;
        display: inline-block;
        font-weight: 600;
        line-height: 1;
        padding: 8px 12px;
        position: relative;
        top: 30px;
        left: -325px;
      }
      h2 {
        font-size: 29px;
        font-weight: 600;
        margin-top: 30px;
      }
      p {
        line-height: 1.5;
      }
      .footer {
        background-color: transparent;
        margin: 16px 0 0 0;
        a {
          color: #0067b8;
          font-weight: 700;
          text-decoration: none;
        }
      }
    }
  }
  .business_card {
    display: flex;
    .card_body {
      display: flex;
      width: 350px;
      height: 440px;
    }
  }
  .card_img {
    width: 320px;
    height: 180px;
  }
  h2 {
    font-size: 29px;
    font-weight: 600;
    margin: 16px 0;
  }
  p {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
  }
}

.one-container {
  position: relative;
  cursor: pointer;
  .card-background {
    width: 100%;
    margin: 0 0.013px;
    padding: 0 75px;
  }
  .card-text {
    z-index: 1;
    width: 440px;
    left: 200px;
    position: absolute;
    top: 140px;
    h1 {
      font-size: 32px;
      font-weight: 600;
      margin-bottom: 20px;
    }
    p {
      font-size: 16px;
      margin-bottom: 20px;
    }
    .onedrive-btn {
      outline: none;
      padding: 8px 16px;
      background-color: #0067b8;
      color: white;
      border: 0px;
      border-radius: 2px;
      font-weight: 600;
    }
  }
}
.follow_request {
  display: flex;
  padding: 0 75px;

  .follow_icon {
    display: flex;
    img {
      width: 32px;
      height: 32px;
    }
  }
}
.move_top {
  display: flex;
  justify-content: flex-end;
  .move_top_btn {
    font-weight: 600;
    padding: 10px 12px;
    border: 0px;
    border-radius: 2px;
    line-height: 1;
    cursor: pointer;
    font-size: 16px;
  }
}
.footer {
  background-color: #f2f2f2;
  .more_info {
    display: flex;
    justify-content: space-between;
    padding: 0 75px;
    p {
      color: #616161;
      font-size: 15px;
      padding: 36px 4px 16px 8px;
      font-weight: 600;
      line-height: 20px;
    }
    ul li {
      font-size: 11px;
      padding: 8px 8px 16px 8px;
    }
  }
  .base {
    padding: 30px 75px;
    justify-content: space-between;
    font-size: 11px;
    display: flex;
    ul {
      display: flex;
      .bold {
        font-weight: 700;
      }
      li {
        padding: 0 24px 4px 0;
        justify-content: center;
      }
    }
  }
}
